.allClassSkills {
  min-height: 100vh;
  background-color: #faf6d9;
  display: flex;
  justify-content: center;
}

.conteiner {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}


.skills {
  width: 100%;
  height: 90%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}



@media screen and (max-width: 790px) {
  .skills {
 justify-content: center;
    flex-direction: column;
  }
}
