.main {
  height: 100vh;
  width: 100%;
  background-image: url(https://s2.best-wallpaper.net/wallpaper/1920x1080/1607/Dolomites-sunrise-snow-winter-Italy_1920x1080.jpg);
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

}

.container {
  width: 100%;
  display: flex;
  background-color: rgba(37, 37, 39, 0.55);
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;

}

.greeting{
  display: flex;
  flex-direction: column;
}
.greeting span:nth-child(1){
  font-size: 19px;

  /*position: relative;*/
