.greeting span:nth-child(2),
.greeting span:nth-child(2) span{
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-stretch: normal;
  font-size: 44px;
  line-height: 1.6;
  letter-spacing: 4px;
  position: relative;
  z-index: 0;
}

.greeting span:nth-child(2) span:before{
  position: absolute;
  content: '';
  width: 374px;
  height: 20px;
  background-color: #e8c792;
  bottom: 3px;
  z-index: -3;
  /*padding: 50px 0;*/
}
