.h1{
  font-size: 18px;
}
.image{
  height: 270px;
  width: 270px;
  background-color: #feefea;
  border-radius: 50%;
}
.photo{
  border-radius: 50%;
  /*border: 3px solid;*/
  /*color: #e8c792;*/
  position: relative;
  z-index: 1;
}
.photo .image:before{
  position: absolute;
  content: '';
  height: 277px;
  width: 280px;
  background-color: rgba(48, 48, 50, 0.46);
  border-radius: 50%;
  bottom: -3px;
  /*border: 3px solid;*/
  /*color: #ff7b30;*/
  z-index: -1;
}
@media screen and (max-width: 480px) {
  .greeting span:nth-child(2) span:before{
    width: 235px;
    margin-left: -95px;
  }
}


@media screen and (max-width: 375px) {
    .container {
    padding: 70px 0;
  }
  .greeting{
    align-items: center;
    font-size: smaller;
  }
  /*.greeting span:nth-child(2):before{ оранжевая линия*/
  /*  width: 200px;*/
